<template>
     <div class="content">
        <van-nav-bar
        title=""
        left-text=""
        right-text=""
        left-arrow
        @click-left="goBack"
        />
        <div class="pay_way_group">
            <van-cell-group class="payment_group">
                <van-cell title="">{{task.tbUser}}</van-cell>
                <van-cell title="">{{task.orderNo}}</van-cell>
                <van-cell title="">{{goods.shop_name}}</van-cell>
                <van-cell title="">{{task.goodsNum}}</van-cell>
                <van-cell title="">{{task.tbMoney}}</van-cell>
                <van-cell title="">{{task.brokerage}}</van-cell>
                <van-cell title=""><img width="100%" v-lazy="goods.main_img" @click="showImagePreview(goods.main_img)" /></van-cell>
            </van-cell-group>
        </div>

        <div class="pay_way_group">
            <van-cell-group>
               <van-cell class="tip_r" title=""></van-cell>
               <van-cell v-if="task.commentType == 1" class="tip_r" title="">5</van-cell>
               <van-cell v-if="task.commentType == 2" class="tip_r" title="">5</van-cell>
               <van-cell v-if="task.commentType == 3" class="tip_r" title="">5~!</van-cell>
            </van-cell-group>
        </div>
        
        <div v-if="task.commentType == 3" class="pay_way_group">
            <van-cell-group>
                 <van-cell title="">
                   {{task.commentBody}}
                   <div v-if="task.commentBody!=''" v-clipboard:copy="task.commentBody" v-clipboard:success="onCopy" v-clipboard:error="onError" class="tip_r"></div>
                 </van-cell>
                 <van-cell title="">{{task.CommentMoney}}</van-cell>
            </van-cell-group>
            <div class="pic_list">
                <div class="pic_title"></div>
                <div class="flex">
                    <img  v-for="(com ,i) in task.commentPic" :key="i" @click="showImagePreview(com)" :src="com" />
                </div>
            </div>
        </div>
        <div class="pay_way_group">
          <van-cell-group>
              <div class="upload-title"></div>
              <div class="upload-desc"></div>
              <div class="demo-pic">
                 <img :src="demoImg" @click="showImagePreview(demoImg)" width="50%">
              </div>
            </van-cell-group>
        </div>

        <!-- <commentUpload :comment_pic="task.comTaskPic" :status="task.commentStatus" @setFiles="setFiles" ref="commentUpload"/> -->
     
        <div class="btn_list">
            <van-button v-if="task.commentStatus!=1 && task.commentStatus!=2" class="btn" @click="subComment" :loading="isSubmit" type="danger" :bottom-action="true"></van-button>
            <van-button class="btn" :loading="false" type="default" @click="goBack" :bottom-action="true"></van-button>  
        </div>
     
     </div>
</template>

<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field
} from "vant";

import commentUpload from "./comment-upload";
import { SALES, COMMENTS } from "@/api/task";

export default {
  name: "Comments",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [commentUpload.name]: commentUpload,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview
  },
  props: {
    id: this.id
  },
  created() {
    this.initPage();
  },
  data() {
    return {
      isSubmit: false,
      goods: [],
      task: [],
      files:'',
      demoImg: require(`@/assets/images/hp.jpg`)
    };
  },
  methods: {
    initPage() {
      if (!this.id) {
        Toast.fail("");
        return;
      }
      this.$reqGet(SALES + this.id)
        .then(res => {
          this.getTaskBack(res);
        })
        .catch(err => {
          this.getTaskBack(err);
        });
    },
    //
    getTaskBack(res) {
      let result = res.data;
      if (result.code != 0) {
        let msg = result && result.msg ? result.msg : "";
        Toast.fail(msg);
        return;
      }
      let data = result.data;
      this.task = data.task;
      this.goods = data.goods;
    },
    //
    subComment() {
      // if(!this.files.comment_file){
      //   Toast.fail("");
      //   return;
      // }
      let formdata = new FormData();
      //formdata.append("comment_pic", this.files.comment_file.file);
      formdata.append("order_id", this.id);
      this.isSubmit = true;
      this.$reqPost(COMMENTS, formdata)
        .then(res => {
          this.commentBack(res);
        })
        .catch(err => {
          this.commentBack(err);
        });
    },
    //
    commentBack(res) {
      this.isSubmit = false;
      let result = res.data;
      if (result.code != 0) {
        let msg = result && result.msg ? result.msg : "";
        Toast.fail(msg);
        return;
      }
      let gainMoney = this.task.brokerage;
      let tipStr = `<p style="text-align: center;">12</p>
      <p style="text-align: center;">${gainMoney}</p>
      <p style="text-align: center;">~</p>`;
      Dialog.alert({
        title: "",
        message: tipStr
      })
        .then(() => {
          this.$router.push({name: 'orders',params: { source: 1 }});
        })
        .catch(err => {});
    },
    onCopy(e) {
      Toast("");
    },
    onError() {
      Toast("");
    },
    //
    goBack() {
      this.$router.push({name: 'orders',params: { source: 1 }});
    },
    //
    showImagePreview(img) {
      ImagePreview([img]);
    },
    setFiles(file){
      console.log(file)
      this.files = file
    },
  }
};
</script>

<style>
.pay_way_title {
  font-weight: bold;
  padding: 15px;
  background-color: #fff;
}
.tip_r {
  color: red;
}
.pic_list {
  padding: 10px 15px;
  background-color: #fff;
}
.flex {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  margin-top: 15px;
}
.flex img {
  width: 80px;
  height: 80px;
  margin: 8px 4px 0 0;
}
.pic-box {
  width: 100%;
  overflow-y: scroll;
}
.btn {
  margin-bottom: 15px;
}
.btn_list {
  padding: 20px;
  background-color: #fff;
}
.upload-title {
  color: red;
  padding: 12px;
  font-size: 16px;
}
.upload-desc{
  font-size: 16px;
  font-weight: bold;
  padding: 15px 0;
  color: darkred;
  text-align: center;
}
.demo-pic{
  text-align: center;
}
</style>



